@import url('https://fonts.googleapis.com/css?family=Josefin+Sans');
@import url('https://fonts.googleapis.com/css?family=Lato:300,400');

html,body {
	font-family: 'Josefin Sans', sans-serif;
	background-color: #F4F4F4;
}
body {
	min-height: 100vh;
}
h1 {

	font-weight: 700; 
	font-style: normal; 

	letter-spacing: -0.04em;
	font-size: 30px;
	line-height: 1.1;
	color: #333332;
}

.wrapper {
	min-height: 100vh;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-direction: column;
	    flex-direction: column;
}
.main {
	width : 100%;
	/*background-color: #FFFFFF;*/
	-ms-flex-item-align: center;
	    align-self: center;
	min-height: 400px;
	margin-bottom: 20px;
	-webkit-box-shadow: 5px 5px 40px 5px rgba(222,222,222,0.5);
	        box-shadow: 5px 5px 40px 5px rgba(222,222,222,0.5);	
	display: -ms-flexbox;	
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	padding-top: 20px;
	position :relative;
}

.footer {
	height: 30px;
	display: -ms-flexbox;
	display: flex;
	background-color: #ffff;
	width: 100%;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	color : #AAAAAA;
	
}
.light {
	font-size: 25px;
	color: #dadada;
}
.bubble-gum-red {
	color : #FF93AC;
}
.white {
	color : #fff;
}
h2 {
	color: #898989;
	font-weight: 100;
	text-transform: uppercase;
	font-size: 15px;
}
.section {
	min-width: 300px;
	max-width: 450px;
}
.section ul {
	list-style: none;
	/*padding : 0;*/
	margin : 0;
}
.image {
	width : 50px;
	height: 50px;
	border-radius: 5px;
	-ms-flex-item-align: center;
	    align-self: center;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.red {
	background-color: #FF6161;
	color : #fff !important;
}
.hover:hover {
	cursor: pointer;
	-webkit-transform : scale(1.013);
	    -ms-transform : scale(1.013);
	        transform : scale(1.013);
	-webkit-box-shadow: 5px 5px 25px 0px rgba(46,61,73,0.12);
	        box-shadow: 5px 5px 25px 0px rgba(46,61,73,0.12);
}
.tags {
	display: -ms-flexbox;
	display: flex;
}
.yellow {
	background-color: #F1FFB9;
}
.light-blue {
	background-color: #DFF4FF
}
a {
	text-decoration: none;
}
.green {
	background-color: #74F2AC;
}
.green-color {
	color: #74F2AC;
}

.yellow {
	background-color: #FDF39C;
}
.blue {
	background-color: #7CD3FC;
}
.orange {
	background-color: #FDA65F;
}
.no-padding {
	padding: 0;
}
.contact {
	list-style: none;
	display: -ms-flexbox;
	display: flex;
	padding: 0;

}
.contact li {
	margin-left: 10px;
	font-size: 20px;
	color: #454343;
}
.image {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.image img {
	width : 100%;
	border-radius: 5px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;

}
.purple {
	background-color: #B962F6;
}
.alternative-blue {
	background-color: #297DF0;
}
.gray {
	background-color: #878686;
}
.alternative-orange {
	background-color: #FCD22A;
}
.repo , .contact li{
	color : #888;
	-webkit-box-shadow: 5px 5px 40px 5px rgba(222,222,222,1);
	        box-shadow: 5px 5px 40px 5px rgba(222,222,222,1);	
	font-size: 25px;
	border-radius: 50px;
	margin-left: 20px;
	-webkit-transition : -webkit-transform .5s ease;
	transition : -webkit-transform .5s ease;
	-o-transition : transform .5s ease;
	transition : transform .5s ease;
	transition : transform .5s ease, -webkit-transform .5s ease;
}
.repo:hover , .contact li:hover {
	-webkit-transform : scale(1.2);
	    -ms-transform : scale(1.2);
	        transform : scale(1.2);
	-webkit-box-shadow: 5px 5px 25px 0px rgba(46,61,73,0.12);
	        box-shadow: 5px 5px 25px 0px rgba(46,61,73,0.12);
}
.post-section {
	background-color: #fff;
	width : 60%;
	padding: 20px;
	margin:0 auto;
	margin-top: -40px;
}
.post-section p {
	font-size: 20px;
	font-weight: 300;
	font-family: 'Lato';
}
.post-title {
	padding-bottom: 10px;
}
.post-image {
	margin: 0 auto;
	width : 300px;
	-webkit-box-shadow: 5px 5px 40px 5px rgba(222,222,222,0.5);
	        box-shadow: 5px 5px 40px 5px rgba(222,222,222,0.5);	

}
.post-image p {
	margin : 0;
	text-align: center;
	color: #777;
	font-size: 15px;
	padding: 10px;

}
.contact li a  {
	color : #444;
	font-size: 20px;
}

.brand span, .brand i  {
	margin-left : 5px;
}
.main-idea {
	padding: 20px;
	padding-top: 0;
	font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.main-idea h1{
	margin-top: 5px;
	text-align:center;
}
.main-idea p {
	text-align: justify;
}


